.contact-container {
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.about-me-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    gap: 1.5rem;
}

.about-me-title {
    color: #303438;
    border-bottom: 1px solid #303438;
    width: 5rem;
    font-size: 1.15rem;
}

.about-me-content-one, .about-me-content-two, .about-me-content-three {
    margin: 0 1.50rem;
    color: #303438;
}

.contact-form-container {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
}

.contact-title {
    color: #303438;
    border-bottom: 1px solid #303438;
    width: 6rem;
    font-size: 1.15rem;
}

.first-name-form {
    border: none;
    border-bottom: 1px solid #303438;
    height: 2.25rem;
    width: 16rem;
    font-size: 1.15rem;
    color: #303438;
}

.last-name-form {
    border: none;
    border-bottom: 1px solid #303438;
    height: 2.25rem;
    width: 16rem;
    font-size: 1.15rem;
    color: #303438;
}

.email-form {
    border: none;
    border-bottom: 1px solid #303438;
    height: 2.25rem;
    width: 16rem;
    font-size: 1.15rem;
    color: #303438;
}

.message-form {
    border: none;
    border-bottom: 1px solid #303438;
    height: 2.25rem;
    width: 16rem;
    font-size: 1.15rem;
    color: #303438;
}

.first-name-form:focus {
    outline: none;
}

.first-name-form::placeholder {
    color: #303438;
}

.last-name-form:focus {
    outline: none;
}

.last-name-form::placeholder {
    color: #303438;
}

.email-form:focus {
    outline: none;
}

.email-form::placeholder {
    color: #303438;
}

.message-form:focus {
    outline: none;
}

.message-form::placeholder {
    color: #303438;
}

.contact-me-btn {
    border: none;
    background-color: #202131;
    color: #EFEFEF;
    padding: 10px 26px;
    font-size: 16px;
    cursor: pointer;
}

@media screen and (min-width: 768px) {
    .about-me-content {
        font-size: 1.15rem;
    }
}

@media screen and (min-width: 992px) {
    .contact-container {
        gap: 2rem;
    }

    .about-me-container {
        gap: 2rem;
    }

    .about-me-content {
        margin: 0 5.25rem;
        font-size: 1.15rem;
    }

    .contact-form-container {
        gap: 2rem;
    }
}

@media screen and (min-width: 1200px) {
    .about-me-content {
        font-size: 1.15rem;
    }
}